<template>
  <div>
    <el-card>
      <div slot="header">
        基本信息
        <el-button
          style="float: right"
          type="info"
          size="small"
          @click="$router.go(-1)"
          >返回</el-button
        >
      </div>
      <div>
        <!-- :rules="rules" -->
        <el-form
          :model="Form"
          :rules="rule"
          ref="formRef"
          label-width="200px"
          class="demo-ruleForm"
        >
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="负责人" required prop="patentee">
                <el-select
                  v-model="Form.patentee"
                  filterable
                  placeholder="请输入内容"
                >
                  <el-option
                    v-for="item in Options"
                    :key="item.id"
                    :label="item.teaName"
                    :value="item.teaId"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="专利类型" required prop="patentType">
                <el-select v-model="Form.patentType">
                  <el-option label="发明专利" value="发明专利"></el-option>
                  <el-option label="实用新型" value="实用新型"></el-option>
                  <el-option label="外观设计" value="外观设计"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="专利范围" required prop="patentScope">
                <el-select v-model="Form.patentScope">
                  <el-option label="国内" value="国内"></el-option>
                  <el-option label="国外" value="国外"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="专利状态" required prop="patentStatus">
                <el-select v-model="Form.patentStatus">
                  <el-option label="专利申请" value="专利申请"></el-option>
                  <el-option label="专利公开" value="专利公开"></el-option>
                  <el-option label="专利授权" value="专利授权"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="申请号" required prop="applicationNumber">
                <el-input v-model="Form.applicationNumber"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item
                label="申请日期"
                required
                prop="applyTime"
              >
                <el-input v-model="Form.applyTime"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item
                label="公开号"
                required
                prop="openCount"
              >
                <el-input v-model="Form.openCount"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="公开日期" required prop="openTime">
                <el-input v-model="Form.openTime"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item
                label="授权号"
                required
                prop="authorizationNumber"
              >
                <el-input v-model="Form.authorizationNumber"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="授权日期" required prop="authorizationTime">
                <el-input v-model="Form.authorizationTime"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="学校署名" required prop="schoolSignature">
                <el-select v-model="Form.schoolSignature">
                  <el-option label="第一单位" value="第一单位"></el-option>
                  <el-option label="非第一单位" value="非第一单位"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="申请人（显示值为学校名）" required prop="patentApplicant">
                <el-input v-model="Form.patentApplicant"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="是否失效" required prop="isInvalid">
                <el-select v-model="Form.isInvalid">
                  <el-option label="是" value="是"></el-option>
                  <el-option label="否" value="否"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="合作类型" required prop="cooperationType">
                <el-select v-model="Form.cooperationType">
                  <el-option label="独立申请" value="独立申请"></el-option>
                  <el-option label="合作申请" value="合作申请"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item
                label="是否为职务专利"
                required
                prop="isJobPatent"
              >
              <el-select v-model="Form.isJobPatent">
                  <el-option label="是" value="是"></el-option>
                  <el-option label="否" value="否"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item
                label="是否为PCT专利"
                required
                prop="isPct"
              >
              <el-select v-model="Form.isPct">
                  <el-option label="是" value="是"></el-option>
                  <el-option label="否" value="否"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="备注" prop="remarksInfo">
                <el-input v-model="Form.remarksInfo"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
    </el-card>

    <el-card>
      <div slot="header">
        作者信息
        <!-- 新增 -->
        <span>
          <el-tooltip
            effect="dark"
            content="新增"
            placement="right"
            :enterable="false"
          >
            <!-- v-if="is_authority.is_userDel" -->
            <el-button
              class="add"
              type="primary"
              icon="el-icon-plus"
              v-if="is_authority.is_user_add"
              size="mini"
              @click="authAdd"
              >点击增加</el-button
            >
          </el-tooltip>
        </span>
      </div>
      <div>
        <el-table :data="authForm" border stripe>
          <el-table-column prop="authorType" label="作者类型" align="center" width="150px">
            <template slot-scope="scope">
              <el-select v-model="scope.row.authorType">
                <el-option
                  label="本校老师"
                  value="本校老师">
                </el-option>
                <el-option
                  label="本校学生"
                  value="本校学生">
                </el-option>
                <el-option
                  label="外校人员"
                  value="外校人员">
                </el-option>
              </el-select>
            </template>
          </el-table-column>

          <el-table-column prop="authorName" label="* 作者姓名" align="center" width="150px">
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorName"></el-input>
            </template>
          </el-table-column>

          <el-table-column
            width="150"
            prop="authorId"
            label="作者编号"
            align="center"
          >
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorId"></el-input>
            </template>
          </el-table-column>

          <el-table-column
            width="80"
            prop="authorSex"
            label="性别"
            align="center"
          >
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorSex"></el-input>
            </template>
          </el-table-column>

          <el-table-column prop="authorEducation" label="学历" align="center">
            <template slot-scope="scope">
              <el-select v-model="scope.row.authorEducation" placeholder="请选择学历">
                  <el-option
                    label="博士研究生"
                    value="博士研究生">
                  </el-option>
                  <el-option
                    label="硕士研究生"
                    value="硕士研究生">
                  </el-option>
                  <el-option
                    label="本科"
                    value="本科">
                  </el-option>
                  <el-option
                    label="专科"
                    value="专科">
                  </el-option>
                  <el-option
                    label="高中及其以下"
                    value="高中及其以下">
                  </el-option>
                  <el-option
                    label="其他"
                    value="其他">
                  </el-option>
                </el-select>
            </template>
          </el-table-column>

          <el-table-column
            width="100"
            prop="signingOrder"
            label="* 署名顺序"
            align="center"
          >
            <template slot-scope="scope">
              <el-input
                v-model="authForm[scope.$index].signingOrder"
              ></el-input>
            </template>
          </el-table-column>

          <el-table-column v-if="this.istea == 1" prop="authorTitle" label="职称" align="center">
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorTitle"></el-input>
            </template>
          </el-table-column>

          <el-table-column
          v-if="this.istea == 1"
            prop="authorWorkUnit  "
            label="工作单位"
            align="center"
          >
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorWorkUnit"></el-input>
            </template>
          </el-table-column>

          <el-table-column
            prop="contributionRate"
            label="贡献率：（ ）单位：%"
            align="center"
            ><template slot-scope="scope">
              <el-input
                v-model="scope.row.contributionRate"
              ></el-input> </template
          ></el-table-column>

          <el-table-column label="操作" width="120px" align="center">
            <template slot-scope="scope">
              <!-- 删除 -->
              <el-tooltip
                class="item"
                effect="dark"
                content="删除"  v-if="is_authority.is_user_del"
                placement="top-start"
                :enterable="false"
              >
                <!-- v-if="is_authority.is_userDel" -->
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="delAuthRow(scope, authForm)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-button class="save" type="primary" @click="submitForm"
        >保存
      </el-button>
    </el-card>

    <el-card>
      <div slot="header">
        附件信息
        <el-upload
          :file-list="fileList"
          class="upload-demo"
          action="http://www.dayanhulian.com:8001/matter/fileoss/uploadOssFile"
          multiple
          :headers="config"
          :on-success="saveUrl"
          :before-upload="beforeUpload"
          accept=".jsp,.png"
        >
          <el-button icon="el-icon-upload2" size="small" type="primary"
            >点击上传</el-button
          >
          <span slot="tip" class="el-upload_Tip">只能上传jpg/png文件</span>
        </el-upload>
      </div>


      <el-dialog
          :visible.sync="dialogVisible">
          
          <img width="100%" :src="photoView" >

      </el-dialog>


      <div
      v-if="this.Id"
      >
        <el-table :data="accessory" border stripe>
          <el-table-column label="附件" align="center">
            <template slot-scope="scope">
              <img @click="photo(scope.row)" :src="scope.row.url" alt=""  style="width: 80px;height: 80px">
            </template>
          </el-table-column>

          <el-table-column label="操作" width="180px" align="center">
            <template slot-scope="scope">
              <el-tooltip
                class="item"
                effect="dark"
                content="预览"
                placement="top-start"
                :enterable="false"
              >
                  <el-button
                    @click="photo(scope.row)"
                    type="primary"
                    icon="el-icon-view"
                    size="mini"
                  ></el-button>
              </el-tooltip>
              
              <el-tooltip
                class="item"
                effect="dark"
                content="下载"
                placement="top"
                :enterable="false"
              >
                  <a :href="scope.row.url" download="附件">
                      <el-button
                          class="download"
                          type="primary"
                          icon="el-icon-download"
                          size="mini"
                    ></el-button>
                  </a>
              </el-tooltip>


              <el-tooltip
                class="item"
                effect="dark"
                content="删除"  v-if="is_authority.is_user_del"
                placement="top-start"
                :enterable="false"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="delAccessory(scope, accessory)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-card>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      rule:{
        patentee: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        patentType: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        patentScope: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        patentStatus: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        applicationNumber: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        applyTime: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        openCount: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        openTime: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        authorizationNumber: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        authorizationTime: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        schoolSignature: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        patentApplicant: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        isInvalid: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        cooperationType: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        isJobPatent: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        isPct: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        paperLayout: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        isTranslationPaper: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        issnNumber: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        cnNumber: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        paperKeywords: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        paperAbstract: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        paperLayout: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ]
      },
      photoView:'',
      dialogVisible: false,
      fileList: [],
      accessory: [],
      Options: [],
      Form: {},
      authForm: [],
      List: [],
      istea: this.$route.query.istea,
      token: window.sessionStorage.getItem('token'),
      Id: this.$route.query.id,
      newAddId: undefined
    }
  },
  computed: {
    ...mapState(['btus']),
    config() {
      return { Token: this.token }
    }
  },
  created() {
    this.isAuthority()
    this.getDetail()
    this.getTreeselect()
  },
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    photo(row){
      this.dialogVisible = true
      this.photoView=row.url
    },
    beforeUpload(file){
      if (!this.Id && !this.newAddId) {
        this.$message.error('请先保存作者信息!')
        return false
      }else{
        console.log(file.type);
        const isjpg = file.type === 'image/png' ||file.type === 'image/jpeg';
        if (!isjpg) {
          this.$message.error('请上传 JPG或者PNG 格式!');
        }
        return isjpg
      }
    },
    async saveUrl(response, file, fileList) {
      this.$http.post('/accessoriesList/saveOrUpdateAccessoriesList', {
        proId: this.Id || this.newAddId,
        url: response.data.url
      })
      this.getDetail()
    },

    authAdd() {
      this.authForm.splice(this.authForm.length, 0, {
        authorEducation: "",
        authorId: "",
        authorName: "",
        authorSex: "",
        authorTitle: "",
        authorType: "",
        authorWorkUnit: null,
        contributionRate: "",
        id: "",
        patentId: this.$route.query.id,
        signingOrder: 1,
      })
    },

    delAccessory(scope, rows) {
      const userid = scope.row.id.split(',')
      console.log(userid)
      this.$confirm('确认要删除吗?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await this.$http.delete(
            '/accessoriesList/removeByIdAccessoriesList/' + userid
          )
          // console.log(res);
          this.getDetail()
        })
        .then(() => {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
        })
    },

    delAuthRow(scope, rows) {
      // console.log(scope);
      if (scope.row.id) {
        const userid = scope.row.id.split(',')
        console.log(userid)
        this.$confirm('确认要删除吗?', '警告', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(async () => {
            await this.$http.delete(
              '/patentAuthorInfo/removeByIdPatentAuthorInfos',
              {
                data: userid
              }
            )
            // console.log(res);
            this.getDetail()
            this.Form = []
          })
          .then(() => {
            this.$message({
              showClose: true,
              message: '删除成功',
              type: 'success'
            })
          })
      } else {
        rows.splice(scope.$index, 1)
      }
    },

    async getTreeselect() {
      this.istea = this.$route.query.istea
      const id = this.$route.query.id
      // console.log(istea);
      if (this.istea == 1) {
        const { data: res } = await this.$http.post(
          '/teaBaseInfo/queryTeaBaseInfoList',
          {
            page: {
              current: 1,
              size: 999
            }
          }
        )
        this.Options = res.data.dataList.records
        if (res.code != 20000) return this.$message.error(res.message)
        // console.log(this.Options)
        // console.log(this.majorOptions)
      } else if (this.istea == 0) {
        const { data: res } = await this.$http.post(
          '/studentInfo/queryStudentInfoList',
          {
            page: {
              current: 1,
              size: 999
            }
          }
        )
        let a = res.data.dataList.records
        a = JSON.parse(JSON.stringify(a).replace(/stuId/g, 'teaId'))
        this.Options = JSON.parse(
          JSON.stringify(a).replace(/stuName/g, 'teaName')
        )
        if (res.code != 20000) return this.$message.error(res.message)
        // console.log(this.Options)
        // console.log(this.majorOptions)
      }
    },

    async getDetail() {
      // console.log(this.Id);
      if (!this.Id) {
        return
      }
      // console.log(id)
      const { data: res } = await this.$http.get(
        '/patentInfo/getByIdPatentInfo/' + this.Id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.Form = res.data.entity
      console.log(this.Form)
      this.getAutherinfo()
      this.getAccessory()
    },

    async getAccessory() {
      const { data: res } = await this.$http.get(
        '/accessoriesList/getByIdAccessoriesList/' + this.Form.id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.accessory = res.data.entity
      console.log(this.accessory)
    },

    async getAutherinfo() {
      const { data: res } = await this.$http.get(
        '/patentAuthorInfo/queryPatentAuthorInfoList/' + this.Form.id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.authForm = res.data.dataList
      console.log(this.authForm)
    },
    submitForm() {
      if (this.istea == 1) this.Form.isStu = 1
      else if (this.istea == 0) this.Form.isStu = 0
      this.$refs.formRef.validate(async valid => {
        if (valid) {
          for (let index = 0; index < this.authForm.length; index++) {
            if (!this.authForm[index].authorName  || !this.authForm[index].signingOrder) {
              return this.$message.error('请输入作者姓名和作者署名')
            }

          }
          await this.$http.post('/patentInfo/saveOrUpdatePatentInfo', this.Form)

          if (!this.Id && this.istea == 1) {
            const { data: res } = await this.$http.post(
              '/patentInfo/queryTeaPatentInfoList',
              {
                page: {
                  current: 1,
                  size: 999
                }
              }
            )
            this.newAddId = res.data.dataList.records.pop().id
            // console.log(Id)
          } else if (!this.Id && this.istea == 0) {
            const { data: res } = await this.$http.post(
              '/patentInfo/queryStuPatentInfoList',
              {
                page: {
                  current: 1,
                  size: 999
                }
              }
            )
            this.newAddId = res.data.dataList.records.pop().id
          }

          for (let index = 0; index < this.authForm.length; index++) {
            if (!this.Id) {
              this.authForm[index].patentId = this.newAddId
              this.authForm[index].signingOrder = 1
            }
            var { data: res1 } = await this.$http.post(
              '/patentAuthorInfo/saveOrUpdatePatentAuthorInfo',
              this.authForm[index]
            )
          }
          if(!res1) return this.$message.error('请填写作者信息!')
          if (res1.code != 20000) return this.$message.error(res1.message)
          else {
            return this.$message.success(res1.message)
            this.getDetail()
            this.$router.go(-1)
          }
        }
      })
    }
  }
}
</script>
